body{
    padding:3rem;
    background : radial-gradient(circle at center ,white,rgb(219, 231, 233));
}


.tarjeta{
    background-image: linear-gradient( to bottom ,   #c09b36 , transparent );
    border-radius:6rem;
    margin:1rem;
    border:2px solid #b1737362;
    

}

.btnElegirPrenda{
    cursor:pointer;
    height:100%;
    width:100%;
}

.icono{
    width:4rem;
     
}
.iconoImgModificar{
    width:1.7rem;
}

.iconoImg:hover{
    height:30px;
    filter:  brightness(500%);
   
}

.iconoImgModificar:hover{
    height:30px;
    filter:  brightness(50%);
   }



.centrado{
    text-align:center;
    
}

td{
    border-bottom: none;
}

table{
    table-layout: fixed;
}


.div-padre{
    display:flex;
    flex-direction: row;
    height:100vh;
    width:100vw;
   

}
.div-col{
    width:calc(36.2% - 6rem);
    margin:1rem;
    border:2px groove black;
   /* overflow: hidden;
    overflow-y: scroll; */
    text-align:center;
}


.div-col-scroll{
    /* border:2px groove black; */
    height:93%;
    overflow: hidden;
    overflow-y: scroll;
}



.div-col-formulario{
    
    width:calc(30% - 6rem);
    border:none;
    overflow:unset;
    overflow-y:unset;
    background :none;
    text-align:center;
      
}


 
#resultado .divResContainer{
    height:80%;
}
#resultado .divResultado{
     
    margin-top: 1rem;
    height:calc(100%/3);
}

.centradoVerti{
   
    height:100%
}

.iconoSelect{
    color:red
}


.subtitulo{
    background: linear-gradient(to right,rgb(74, 74, 253),blue,rgb(74, 74, 253));
    padding:0.5rem;
    margin:0 30%;
    margin-bottom: .5rem;
    border-radius:90px
}

 
